export default {
  name: 'expenseManualEdit',
  data () {
    return {
	  fdBerthName:'',
	  fdArrivalPlace:'',
	  fdStartPlace:'',
	  fdTravelDays:'',
      fdBeginDate:'',
      fdRealUserName: '',
      fdExpenseItemName:'',
      fdCostCenterName:'',
      fdHappenDate:'',
      fdMoney:'',
      fdWbsName:'',
