
import Search from 'vux/src/components/search/index.vue'

export default {
data() {
return {
keyword:'',
};
},
components: {
Search
},
methods:{
onSubmit(){
this.$emit('watchSearch',this.keyword);    
},
onCancel(){
this.$emit('watchSearch','');    
},
onClear(){
this.$emit('watchSearch','');
}
},
};



