export default {
  name: "fsOrganization",
  data() {
    return {
      keyword:''
    };
  },
  props: {
    showPanel: {
      type: Boolean,
      default: false
    },
    currentDeptName: {
      type: String,
      default: ""
    },
    currentDeptId: {
      type: String,
      default: ""
    },
    list: {
      type: Array,
      default: []
    },
    multiple: {
      type: Boolean,
      default: true
    },
    fieldName:{
      type:String,
      default:''
    },
    type:{
      type : String,
      default:''
    }
  },
  methods: {
    // 点击取消按钮
    closePop() {
      // 向父组件发起事件 关闭弹出框
      this.$emit("onClosePop");
    },
    // 点击确定按钮
    confirm(){
      this.$emit('onConfirmSelect',this.selectedList)
    },
    // 单选的选择时间
    choose(item){
      if(!this.multiple&&this.type==item.type){
        // console.log('点击了选择')
        this.$emit('onConfirmSelect',{item:item,name:this.fieldName})
      }
    },
    toParent(){
      this.$emit('toParent',{currentDeptId:this.currentDeptId,name:this.fieldName})
    },
    toChild(item){
      this.$emit('toChild',{currentDeptId:item.id,child:true,name:this.fieldName})
    },
    searchOrg(){
      this.$emit('searchOrg',{keyword:this.keyword,name:this.fieldName});
    },
    searchCancel(){
      this.$emit('searchCancel',this.fieldName);
    }
  },
  computed: {
    organList() {
      return this.list
    },
    selectedList() {
      let i = 0,arr=[];
      if (this.organList.length > 0) {
        this.organList.forEach(item => {
          item.list.forEach(childItem => {
            if (childItem.selected) {
              // i++;
              arr.push(childItem)
            }
          });
        });
      }
      return arr;
    }
  }
};



