export default {
  data() {
    return {
      nowStep: 0,
      list: [
        {
          img: require("../../../assets/images/third/taxi-01.jpg"),
          title: "确认行程"
        },
        {
          img: require("../../../assets/images/third/taxi-02.jpg"),
          title: "行程中"
        }
      ]
    };
  },
  activated(){
    this.nowStep = 0
  },
  methods: {
    show(index) {
      if (index === 0) {
        this.nowStep = 1
      } else {
          this.nowStep = 0
      }
    }
  }
};



